import { MathHelper } from "../../engine/tools"
import TWEEN from "@tweenjs/tween.js"
import {
  Container,
  Sprite,
  Texture
} from "pixi.js"

/**
 *
 */
export class Lightning extends Container {
  constructor() {
    super()

    this.pivot.x = this.width / 2
    this.pivot.y = this.height / 2

    const sprite = new Sprite(Texture.WHITE)

    sprite.width = 1000
    sprite.height = 1000
    this.alpha = 0

    setInterval(() => {
      this.shine()
    }, MathHelper.getRandomInt(5, 10) * 1000)

    setInterval(() => {
      this.shine()
    }, MathHelper.getRandomInt(10, 20) * 1000)

    this.addChild(sprite)
  }

  private shine() {
    const start = {
      alpha: this.alpha
    }

    const update = (coords: { alpha: number }) => {
      this.alpha = coords.alpha
    }

    const up = new TWEEN.Tween(start)
      .to({ alpha: 0.75 }, 100)
      .repeat(1)
      .easing(TWEEN.Easing.Exponential.In)
      .onUpdate(update)

    const down = new TWEEN.Tween(start)
      .to({ alpha: this.alpha }, 300)
      .repeat(1)
      .easing(TWEEN.Easing.Quartic.Out)
      .onUpdate(update)

    // play up then down
    up.chain(down)

    up.start(0)
  }
}
