import { AnimationBase } from "../../../engine/objects/animation-base"
import { IResourceDictionary, Sprite } from "pixi.js"

/**
 *
 */
export class Impact extends AnimationBase {
  constructor(
    resources: IResourceDictionary,
    which: string
  ) {
    super(resources)

    const res = this._resources["weapons.impact"]

    const impact = new Sprite(res.textures![which])

    this.addChild(impact)
  }
}
